<template>
  <CChartLine
    :datasets="defaultDatasets"
    :options="defaultOptions"
    :labels="labels"
  />
</template>

<script>
import { CChartLine } from '@coreui/vue-chartjs'
import { getColor, hexToRgba } from '@coreui/utils/src'

export default {
  name: 'MainChart',
  props: [ 'data', 'labels', 'setValue', 'time' ],
  components: {
    CChartLine
  },
  data() {
    return {
      max: 0,
      min: 0,
      
    }
  },
  mounted() {
    
  },
  computed: {
    defaultDatasets () {
      const brandSuccess = getColor('success') || '#4dbd74'
      const brandInfo = getColor('info') || '#20a8d8'
      const brandDanger = getColor('danger') || '#f86c6b'

      const data1 = []
      const data2 = []
      const data3 = []
      
      let liveMax = Math.max(...this.data)
      let liveMin = Math.min(...this.data)
      this.max = liveMax+20
      this.min = liveMin-20

      this.data.forEach(element => {
        data1.push(element)
      })

      if(!this.setValue) {
        for(let i=0; i<this.data.length; i++) {
          data2.push(0)
        }
      } else {
        this.setValue.forEach(element => {
          data2.push(element)
        })
      }
      

      this.time.forEach(element => {
        data3.push(element)
      })
      
      return [
        {
          label: '目前速度',
          backgroundColor: hexToRgba(brandInfo, 10),
          borderColor: brandInfo,
          pointHoverBackgroundColor: brandInfo,
          borderWidth: 2,
          data: data1
        },
        {
          label: '設定速度',
          backgroundColor: 'transparent',
          borderColor: brandSuccess,
          pointHoverBackgroundColor: brandSuccess,
          borderWidth: 2,
          borderDash: [8, 5],
          data: data2
        },
        {
          label: '時間',
          backgroundColor: 'transparent',
          borderColor: '#9A2EFE',
          pointHoverBackgroundColor: '#9A2EFE',
          borderWidth: 2,
          borderDash: [8, 5],
          data: data3
        },
        // {
        //   label: 'UCL',
        //   backgroundColor: 'transparent',
        //   borderColor: brandDanger,
        //   pointHoverBackgroundColor: brandDanger,
        //   borderWidth: 2,
        //   borderDash: [8, 5],
        //   data: data4
        // },
        // {
        //   label: 'LCL',
        //   backgroundColor: 'transparent',
        //   borderColor: '#900C3F',
        //   pointHoverBackgroundColor: '#900C3F',
        //   borderWidth: 2,
        //   borderDash: [8, 5],
        //   data: data5
        // }
      ]
    },
    defaultOptions () {
      return {
        maintainAspectRatio: false,
        legend: {
          display: true
        },
        scales: {
          xAxes: [{
            gridLines: {
              drawOnChartArea: true
            }
          }],
          yAxes: [{
            ticks: {
              beginAtZero: false,
              maxTicksLimit: 5.5,
              stepSize: 0.1,
              max: this.max,
              min: this.min
            },
            gridLines: {
              display: true
            }
          }]
        },
        elements: {
          point: {
            radius: 0,
            hitRadius: 10,
            hoverRadius: 4,
            hoverBorderWidth: 3
          }
        },
      }
    }
  }
}
</script>
